$bg_color_main:		#0E1420;
$bg_color_modal:	#1E2D41;
$bg_color_panel:	#2A2A2A;
$bg_color_element:	#2B5278;
$bg_color_image:	#808080;
$link_color:		#89C5CC;
$font_stack:		Roboto;

body{
	background-color: $bg_color_main;
	font-family: $font_stack;
	font-weight: 400;
	color: #FFF;
	margin: 0 0;
}

h1{font-size: 30px; text-align: center;}
h2{font-size: 18px;}
.link{font-size: 18px;}
.desc{font-size: 12px; color: rgba(255, 255, 255, 0.8); padding-left: 18px;}
.err{font-size: 12px; color: #FF1F1F; padding-left: 18px;}

#chat{
	display: flex;
	width: 100vw;
	height: 100vh;
}

/* ==Frames== */
#central_frame{
	width: calc(100% - 321px);
}

#side_frame, #upper_frame, #bottom_frame, #details_frame, #header_frame{
	background-color: $bg_color_panel;
}

#upper_frame, #header_frame, #bottom_frame{
	display: flex;
	height: 40px;
	width: 100%;
}
#upper_frame, #header_frame{border-bottom: 1px solid #000;}
#bottom_frame{border-top: 1px solid #000;}

.name{
	margin-top: 10;
	margin-left: 18;
	width: 90%;
}

.message_box{
	margin-top: 10;
	width: 90%;
}

.icon{
	margin-top: 2px;
	margin-left: 2px;
	width: 36px;
	height: 36px;
	border-radius: 18px;
	background-color: $bg_color_image;
	background-image: url("/static/icon-36.svg");
}

#messages_frame{
	height: calc(100vh - 82px);
}

#side_frame{
	height: 100%;
	width: 320px;
	border-right: 1px solid #000;
	flex-direction: column;
}

#details_frame{
	height: 100%;
	width: 320px;
	border-left: 1px solid #000;
}

#chat_list{
	flex-direction: column;
}

a:link,a:visited{
	color: $link_color;
	text-decoration: none;
}

button{
	font-size: 18px;
	color: #FFF;
	background-color: $bg_color_element;
	border-style: none;
}

.main_button{
	width: 300px;
	height: 36px;
	border-radius: 15px;
	margin-top: 10px;
	margin-bottom: 10px;
	opacity: 0.7;
	cursor: pointer;
}
.close_button{
	width: 36px;
	height: 36px;
	border-radius: 18px;
	margin-top: 0px;
	margin-left: calc(100% - 36px);
	opacity: 0.7;
	cursor: pointer;
}
.more_button{
	margin: 2px 2px;
	width: 36px;
	height: 36px;
	border-radius: 18px;
	opacity: 0.7;
	cursor: pointer;
}
.search_button, .attach_button{
	width: 36px;
	height: 36px;
	border-style: none;
	background-color: rgba(0, 0, 0, 0);
	background-repeat: no-repeat;
	background-position: 6 6;
	opacity: 0.7;
	cursor: pointer;
}
.search_button{background-image: url("/static/search.svg");}
.attach_button{background-image: url("/static/attachment.svg");}
.main_button:hover, .close_button:hover, .more_button:hover, .search_button:hover, .attach_button:hover{opacity: 1}

.search_form{
	display: flex;
	width: calc(320px - 48px);
	margin-top: 2px;
}

input{
	background: none;
	outline: none;
	color: #FFF;
	padding-left: 18px;
}

.textbox{
	border: none;
	width: 100%;
}

.main_inputbox{
	width: calc(300px - 36px);
	height: 36px;
	border-radius: 15px;
	border-color: $bg_color_element;
	/* background-color: $bg_color_modal; */
	border-style: solid;
}

#home, #no_page{
	margin-left: auto;
	margin-right: auto;
	width: 610px;
	height: 480px;
	margin-top: calc(50vh - 240px);
}

#login, #signup{
	background-color: $bg_color_modal;
	border-radius: 20px;
	margin-left: auto;
	margin-right: auto;
	width: 360px;
	height: 480px;
	margin-top: calc(50vh - 240px);
}

.selector{
	display: flex;
	flex-direction: column;
	width: 300px;
	margin-left: auto;
	margin-right: auto;
}
ul{
	list-style-type: none;
	margin: 0;
	padding: 0;
}

/* == Images ==*/
#people-24,#people-12{
	width: 230px;
	height: 440px;
	border-style: none;
	margin-left: auto;
	margin-right: auto;
}
#people-12{background-image: url("/static/people-12.svg");}
#people-24{background-image: url("/static/people-24.svg");}